.play-pause-metronome-button {
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  display: flex;
}

.number-metronome {
  text-align: center;
  font-size: 30px;
}

.subtext-metronome {
  text-align: center;
  font-size: 20px;
}

.metronome-controls {
  touch-action: manipulation;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.tempo-container {
  margin: 0 15px;
}

.play-metronome-icon {
  border-top: 15px solid #0000;
  border-bottom: 15px solid #0000;
  border-left: 30px solid #e9e9e9;
  width: 0;
  height: 0;
  margin-left: 5px;
}

.pause-metronome-icon {
  box-sizing: revert;
  border: none;
  border-left: 10px solid #e9e9e9;
  border-right: 10px solid #e9e9e9;
  width: 10px;
  height: 30px;
}

.metronome-switch {
  transform: scale(1.4);
}

[data-id="beatsperbar-change"] {
  margin-left: 7px;
  margin-right: 7px;
}

@font-face {
  font-family: Bravura;
  src: url("Bravura.1b76da8b.otf") format("opentype");
}

.music-symbols {
  font-family: Bravura, serif;
  font-size: 34px;
}

@keyframes blink {
  0% {
    background-color: #ffc107;
  }
}
